<template>
  <div id="service">
    <van-nav-bar>
      <div slot="title">
        我的客服
      </div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
    </van-nav-bar>
    <img style="position: fixed;top:0;left:0;width: 100%;height: 100%;" src="../../static/img/service.png"/>
    <p class="content">{{model}}</p>
    <div class="copy" v-on:click="copyUrl">复制</div>
  </div>
</template>

<script>
  export default {
    name: "service",
    data() {
      return {
        model:"15105226477"
      }
    },
    methods:{
      copyUrl() {
        const input = document.createElement('input')
        document.body.appendChild(input)
        input.setAttribute('value',this.model)
        input.select()
        if (document.execCommand('copy')) {
          document.execCommand('copy')
          alert('已复制到粘贴板')
        }
        document.body.removeChild(input)
      }
    },
  }
</script>

<style scoped>
  .copy{
    position: absolute;
    top: 70%;
    left: 58%;
    opacity:0;
    width: 5rem;
    height: 10rem;
    z-index: 1;
    border-radius: 0.5rem;
    padding: 0.15rem 0.22rem;
  }
  .content{
    position: absolute;
    top:78.5%;
    color: #ffffff;
    left: 26%;
    z-index: 3;
    border-radius: 0.5rem;
  }

</style>



